"use client";

import { useEffect, useMemo, useState } from "react";

function cn(...s: Array<string | false | null | undefined>) {
  return s.filter(Boolean).join(" ");
}

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const [loading, setLoading] = useState(false);
  const [msg, setMsg] = useState<string | null>(null);

  // forgot modal
  const [forgotOpen, setForgotOpen] = useState(false);
  const [forgotEmail, setForgotEmail] = useState("");
  const [forgotLoading, setForgotLoading] = useState(false);
  const [forgotMsg, setForgotMsg] = useState<string | null>(null);

  const canSubmit = useMemo(() => {
    return email.trim().length > 0 && password.trim().length > 0;
  }, [email, password]);

  useEffect(() => {
    // se já estiver logado, manda pro home
    (async () => {
      try {
        const res = await fetch("/api/auth/me", { cache: "no-store" });
        const json = await res.json().catch(() => null);
        if (json?.success && json?.user) {
          window.location.href = "/";
        }
      } catch {
        // ignore
      }
    })();
  }, []);

  async function onLogin(e: React.FormEvent) {
    e.preventDefault();
    setMsg(null);

    if (!canSubmit) {
      setMsg("Preencha email e senha.");
      return;
    }

    setLoading(true);
    try {
      const res = await fetch("/api/auth/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });

      const json = await res.json().catch(() => null);

      if (!res.ok || !json?.success) {
        throw new Error(json?.error || "Falha ao fazer login.");
      }

      setMsg(null);
      window.location.href = "/";
    } catch (err: any) {
      setMsg(err?.message || "Erro inesperado.");
    } finally {
      setLoading(false);
    }
  }

  function openForgot() {
    setForgotMsg(null);
    setForgotEmail(email || "");
    setForgotOpen(true);
  }

  async function onForgot(e: React.FormEvent) {
    e.preventDefault();
    setForgotMsg(null);

    const em = forgotEmail.trim().toLowerCase();
    if (!em) {
      setForgotMsg("Digite seu email.");
      return;
    }

    setForgotLoading(true);
    try {
      const res = await fetch("/api/auth/forgot-password", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email: em }),
      });

      const json = await res.json().catch(() => null);

      if (!res.ok || json?.success === false) {
        throw new Error(json?.error || "Não foi possível enviar o link.");
      }

      // ✅ por segurança o endpoint retorna success mesmo se não existir
      setForgotMsg(
        "Se o email estiver cadastrado, você verá o link no terminal (por enquanto)."
      );
    } catch (err: any) {
      setForgotMsg(err?.message || "Erro inesperado.");
    } finally {
      setForgotLoading(false);
    }
  }

  return (
    <main className="min-h-screen w-full flex items-center justify-center px-4">
      <div className="w-full max-w-md bg-[#121212] rounded-2xl p-8 shadow-xl border border-white/5">
        <div className="text-center mb-6">
          <h1 className="text-2xl font-semibold">Entrar</h1>
          <p className="text-sm text-zinc-400 mt-2">
            Acesse sua conta para continuar.
          </p>
        </div>

        <form onSubmit={onLogin} className="space-y-4">
          <input
            type="email"
            placeholder="Email"
            autoComplete="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            className="w-full h-11 rounded-lg bg-[#1A1A1A] border border-white/10 px-4 text-sm focus:outline-none focus:ring-2 focus:ring-[#00FF8B]"
          />

          <input
            type="password"
            placeholder="Senha"
            autoComplete="current-password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className="w-full h-11 rounded-lg bg-[#1A1A1A] border border-white/10 px-4 text-sm focus:outline-none focus:ring-2 focus:ring-[#00FF8B]"
          />

          {msg ? (
            <div className="text-sm text-red-400 bg-red-500/10 border border-red-500/20 rounded-lg px-3 py-2">
              {msg}
            </div>
          ) : null}

          <button
            type="submit"
            disabled={loading || !canSubmit}
            className={cn(
              "w-full h-11 rounded-lg font-medium transition",
              "bg-[#00FF8B] text-black hover:opacity-90",
              (loading || !canSubmit) && "opacity-60 cursor-not-allowed"
            )}
          >
            {loading ? "Entrando..." : "Entrar"}
          </button>

          <div className="flex items-center justify-between pt-2">
            <button
              type="button"
              onClick={openForgot}
              className="text-sm text-[#00FF8B] hover:underline"
            >
              Esqueci a senha
            </button>

            <a href="/register" className="text-sm text-zinc-300 hover:underline">
              Criar conta
            </a>
          </div>
        </form>
      </div>

      {/* Forgot Password Modal */}
      {forgotOpen ? (
        <div
          className="fixed inset-0 z-50 flex items-center justify-center px-4"
          aria-modal="true"
          role="dialog"
        >
          {/* overlay */}
          <div
            className="absolute inset-0 bg-black/70"
            onClick={() => setForgotOpen(false)}
          />

          <div className="relative w-full max-w-md bg-[#121212] rounded-2xl p-6 shadow-xl border border-white/5">
            <div className="flex items-start justify-between gap-4">
              <div>
                <h2 className="text-xl font-semibold">Recuperar senha</h2>
                <p className="text-sm text-zinc-400 mt-1">
                  Informe seu email para gerar o link de redefinição.
                </p>
              </div>

              <button
                onClick={() => setForgotOpen(false)}
                className="text-zinc-400 hover:text-white transition"
                aria-label="Fechar"
              >
                ✕
              </button>
            </div>

            <form onSubmit={onForgot} className="space-y-4 mt-5">
              <input
                type="email"
                placeholder="Email"
                autoComplete="email"
                value={forgotEmail}
                onChange={(e) => setForgotEmail(e.target.value)}
                className="w-full h-11 rounded-lg bg-[#1A1A1A] border border-white/10 px-4 text-sm focus:outline-none focus:ring-2 focus:ring-[#00FF8B]"
              />

              {forgotMsg ? (
                <div
                  className={cn(
                    "text-sm rounded-lg px-3 py-2 border",
                    forgotMsg.includes("Se o email")
                      ? "text-emerald-300 bg-emerald-500/10 border-emerald-500/20"
                      : "text-red-400 bg-red-500/10 border-red-500/20"
                  )}
                >
                  {forgotMsg}
                </div>
              ) : null}

              <button
                type="submit"
                disabled={forgotLoading}
                className={cn(
                  "w-full h-11 rounded-lg font-medium transition",
                  "bg-[#00FF8B] text-black hover:opacity-90",
                  forgotLoading && "opacity-60 cursor-not-allowed"
                )}
              >
                {forgotLoading ? "Enviando..." : "Enviar link"}
              </button>

              <div className="text-center">
                <button
                  type="button"
                  onClick={() => setForgotOpen(false)}
                  className="text-sm text-zinc-300 hover:underline"
                >
                  Voltar
                </button>
              </div>
            </form>
          </div>
        </div>
      ) : null}
    </main>
  );
}
